div p.intro {
  font-size: 1.4rem;
  font-weight: normal;
  letter-spacing: 0.1rem;
  padding: 0.5rem;
}

.tech .typed-text {
  color: #dd7732;
  font-weight: 600;
}

span.cursor {
  display: inline-block;
  visibility: hidden;
  background-color: #ccc;
  width: 3px;
  margin-left: 0.1rem;
  -webkit-animation: blink 1s infinite;
          animation: blink 1s infinite;
}

span.cursor.typing {
  -webkit-animation: none;
          animation: none;
}

@-webkit-keyframes blink {
  0%  { background-color: #ccc; }
  49% { background-color: #ccc; }
  50% { background-color: transparent; }
  99% { background-color: transparent; }
  100%  { background-color: #ccc; }
}

@keyframes blink {
  0%  { background-color: #ccc; }
  49% { background-color: #ccc; }
  50% { background-color: transparent; }
  99% { background-color: transparent; }
  100%  { background-color: #ccc; }
}

.circle {
  visibility: hidden;
  background-color: #dd7732;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  transform: translate(-9.9rem,-1.4rem);
  position: absolute;
  bottom: 0;
  z-index: 99;
}

@-webkit-keyframes animate {
  100% {
    width: 200vmax;
    height: 200vmax;
    transform: translate(-100vmax, 100vmax);
  }
}

@keyframes animate {
  100% {
    width: 200vmax;
    height: 200vmax;
    transform: translate(-100vmax, 100vmax);
  }
}

.fade-out {
	-webkit-animation: fade-out 1s ease-out both;
	        animation: fade-out 1s ease-out both;
}

@-webkit-keyframes fade-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes fade-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.btn {
  color: #ccc;
}

.btn:hover {
  color: #dd7732;
}

