html {
  --background-source: url(/static/media/bio.1caa8e05.jpg);
}

.side-drop-color {
  background-color: #dd7732;
  background-size: cover;
  width: 50%;
  height: 100%;
  position: absolute;
  right: 0;
  transition:
    width .5s linear .6s;
}

.side-drop-color-active {
  width: 100%;
  transition:
    width .5s ease;
}

.side-drop-image {
  background-image: url(/static/media/bio.1caa8e05.jpg);
  background-image: var(--background-source);
  background-size: cover;
  opacity: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  right: 0;
  transition:
    opacity .5s linear .25s,
    background-image .5s linear .25s;
}

.side-drop-image-active {
  opacity: 1;
  transition:
    opacity .5s linear .25s,
    background-image .5s linear .25s;
}

.section-link {
  padding: 0;
  font-size: 3rem;
  font-weight: 900;
  letter-spacing: 0.1rem;
}

.section-link.icons{
  font-size: 4rem;
}

.section-link span {
  display: inline-flex;
  align-items: center;
  height: 100%;
}

.section-link a:link, 
.section-link a:visited,
.section-link a:active {
  color: #ccc;
  mix-blend-mode: soft-light;
  text-shadow: 4px 4px 8px #000;
  text-decoration: none;
}

.section-link a:hover {
  color: #dd7732;
  mix-blend-mode: normal;
  text-decoration: none;
}

@media (min-width: 576px) {
  .section-link {
    font-size: 4rem;
  }
}

@media (min-width: 768px) {
  .section-link,
  .section-link.icons {
    font-size: 5rem;
  }
}

@media (min-width: 992px) {
  .section-link,
  .section-link.icons {
    font-size: 6rem;
  }
}

