html {
  --menu-background-source: url(/static/media/bio.1caa8e05.jpg);
}

#side-menu-container {
  padding: 0;
  /* height: 100vh; */
  background-image: url(/static/media/bio.1caa8e05.jpg);
  background-image: var(--menu-background-source);
  background-size: cover;
  background-position: center;
}

#side-menu-container > div {
  display: inline-flex;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
}

.side-menu {
  font-size: 1.8rem;
  font-weight: 900;
  letter-spacing: 0.1rem;
}

.side-menu a:link, 
.side-menu a:visited,
.side-menu a:active {
  color: #ccc;
  text-decoration: none;
}

.side-menu a:hover {
  color: #dd7732;
  text-decoration: none;
}

.selected a:link, 
.selected a:visited,
.selected a:active,
.selected a:hover {
  color: #dd7732;
  font-size: 130%;
  letter-spacing: 0.1rem;
}

@media (min-width: 576px) {
  #side-menu-container {
      position: relative;
  }
}

@media (min-width: 768px) {
  #side-menu-container {
    height: 100vh;
    position: fixed;
  }
}

