/******************************
*  Resume.js navigation tabs  *
******************************/

.accordion .card {
  color: #ccc;
  background-color: #dd7732;
}

.card-header {
  color: #ccc;
  background-color: #000;
  font-size: 1.5rem;
  font-weight: 600;
}

.card-header:hover {
  color: #dd7732;
  background-color: #000;
  cursor: pointer;
}

.card-body {
  color: #ccc;
  background-color: #000;
}

.show-header {
  color: #dd7732;
}

.card-header span:last-child {
  transition: transform .2s linear;
}

.card-header.show-header span:last-child {
  transform: rotate(90deg);
  transition: transform .2s linear;
}

li.inner::before {
  content: '- ';
}

.border-bottom {
  border-color: #dd7732 !important;
}

small.icons a:link, 
small.icons a:visited,
small.icons a:active,
.download-btn a:link,
.download-btn a:visited,
.download-btn a:active {
  color: #ccc;
  text-decoration: none;
}

small.icons a:hover,
.download-btn a:hover {
  color: #dd7732;
  text-decoration: none;
}

.accordion > .card:last-child > .card-header {
  font-size: 1rem;
  font-weight: normal;
  letter-spacing: 0.1rem;
  justify-content: center !important;
  height: 4rem;
}

.accordion > .card:last-child span {
  display: inline-block;
  margin: auto;
}
/**************************
*  Portfolio.js carousel  *
**************************/

div.carousel {
  border: #ccc 1px solid;
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 50px;
  /* max-width: 768px; */
}

.carousel-caption {
  /* background-color: rgba(0, 0, 0, 0.7); */
  border-radius: 10px;
  margin-top: 1.3rem;
  position: relative;
  left: auto;
  right: auto;
}

.carousel-indicators .active {
  background-color: #dd7732;
}
.carousel-control-next-icon {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='%23dd7732' width='8' height='8' viewBox='0 0 8 8'%3e%3cpath d='M2.75 0l-1.5 1.5L3.75 4l-2.5 2.5L2.75 8l4-4-4-4z'/%3e%3c/svg%3e");
}

.carousel-control-prev-icon {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='%23dd7732' width='8' height='8' viewBox='0 0 8 8'%3e%3cpath d='M5.25 0l-4 4 4 4 1.5-1.5L4.25 4l2.5-2.5L5.25 0z'/%3e%3c/svg%3e");
}

p.icons a:link, 
p.icons a:visited,
p.icons a:active {
  color: #ccc;
  font-size: 2rem;
  text-decoration: none;
}

p.icons a:hover {
  color: #dd7732;
  text-decoration: none;
}
